\n    white-space: nowrap;\r\n    text-transform: uppercase;\r\n    font-weight: 600;\r\n    margin-bottom: 10px;\r\n    padding-top:20px;\r\n    font-size: calc(14px + 6 * ((100vw - 320px) / 300));\r\n    text-align:center ;\r\n}\r\n\r\n.inter h2 {\r\n    display:inline-block;\r\n    width:fit-content;\r\n    padding:0px 10px;\r\n}\r\n\r\n\r\n.inter h2:hover {\r\n    cursor:default;\r\n}\r\n\r\n.inter h2:after {\r\n    display:block;\r\n    content: '';\r\n    border-bottom: solid 2px white; \r\n    transform: scaleX(0);  \r\n    transition: transform 250ms ease-in-out;\r\n}\r\n.inter h2:hover:after { \r\n    transform: scaleX(1); \r\n}\r\n\r\n.inter h2:after{  \r\n    transform-origin: 0% 50%; \r\n}\r\n\r\n\r\n/******************************************\r\n/* INTER\r\n/*******************************************/\r\n\r\n/******************************************\r\n/* MAIN\r\n/*******************************************/\r\n\r\n.shop {\r\n    display:grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    column-gap:20px;\r\n    row-gap: 20px;\r\n    padding-bottom:20px;\r\n    border-bottom:rgba(255, 255, 255, 0.555) 1px solid;\r\n    text-align: center;\r\n}\r\n.item>div {\r\n    background:rgba(0, 0, 0, 0.425);\r\n    padding:8px 10px 20px;\r\n    height: fit-content;\r\n    font-size:calc(10px + 6 * ((100vw - 320px) / 300));\r\n    text-align:center;\r\n    border-radius: 5px;\r\n}\r\n.shop img {\r\n    display:block;\r\n    width:100%;\r\n    height:auto;\r\n    object-fit:cover;\r\n    object-position:center;\r\n}\r\n\r\n.shop p{\r\n    display: inline-block;\r\n    padding:10px 0 2px 0;\r\n    text-align: center;\r\n    font-weight:500;\r\n}\r\n\r\n.priceQuantity .buttons {\r\n    display:none;\r\n    justify-content: center;\r\n}\r\n\r\n.productButtons {\r\n    display:flex;\r\n    justify-content: space-around;\r\n    align-items: center;\r\n    padding-top:10px;\r\n}\r\n.productButtons a{\r\n    display:block;\r\n    height: fit-content;\r\n}\r\n.pro